

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import router from './router'
import App from './App'
import apiConfig from '../config/api.config'
import store from './store'
import Bridge from './config/bridge'
import qs from 'qs'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import Axios from 'axios'





Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(VueRouter)

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')

adaptation(750);


function adaptation(size) {
if (document.documentElement.clientWidth > size) {
document.documentElement.style.fontSize = size / 7.5 + 'px';
} else {
document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + 'px';
}
}

window.onresize = function () {
adaptation(750);
}


Axios.defaults.baseURL = apiConfig.baseUrl;
Axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';


Vue.prototype.$ajax = Axios;
Vue.prototype.$qs = qs;
Vue.prototype.$bridge = Bridge;


Vue.prototype.cnzzTrackEvent = function (name) {
window._czc.push(['_trackEvent', name, "点击"])
}



document.addEventListener("UniAppJSBridgeReady", function () {
uni.getEnv(function (res) {
console.log('当前环境：' + JSON.stringify(res));
});
});


