
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import mobilePopup from "@/components/v-bind-mobile-popup.vue";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
components: {
XButton,
XHeader,
XInput,
Badge,
Checker,
CheckerItem,
mobilePopup,
MescrollVue
},
mounted() {
this.$nextTick(function() {


this.token = localStorage.getItem("token");
this.getInitData();
});
},
data() {
return {
confirmDialog: false,
lock: false,
nickName: "",
token: "",
money: 0,
isWallet: false,
isNewPerson: false,
selectMoney: 0,
newPersonList: "",
newPersonVal: -1,
moneyList: "",
cashVal: -1,
name: "",
moneyNum: "",
business: "",
isShowPopup: false,
popupTitle: "",
popupInfo: "",
popupBtnText: "好的",
isCloseBtn: false,
isToWallet: false,
isToPhone: false,
isToGgk: false,
mescrollDown: {
use: false
},
mescrollUp: {
use: false,
isBounce: false
}
};
},
computed: {},
filters: {
numFilter(value) {

let tempVal = parseFloat(value / 100).toFixed(3);
let realVal = tempVal.substring(0, tempVal.length - 1);
return realVal;
}
},
methods: {
...mapMutations(["setToken"]),
closeWebview() {
console.log(this.$bridge)
this.$bridge.callhandler("closeWebview", {}, data => {

console.log(data);
});

uni.redirectTo({
url: '../index/index'
});
},
getInitData() {
let params = {
token: this.token,
api_name: "user_balance",
package_name: localStorage.getItem("package_name")
};
this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
let data = res.data;
/**
uid 用户id
money 余额
is_perfect_wallet 是否已完善钱包 1：已完善 0：未完善
is_ggk_num_enough 是否已刮满50张卡 1：已刮满 0：未刮满
already_ggk_num 已经刮了的数量
total_ggk_num 总共需要刮满的数量
**/
if (data.code == 1) {
this.money = data.data.money;
this.nickName = data.data.user_nicename;



if (data.data.is_show == 1) {
this.isNewPerson = true;
this.getNewPersonData();
}
this.getMoneyData();
} else {
this.$vux.toast.text(data.msg);
}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
},
getMoneyData() {

let params = {
token: this.token,
api_name: "case_out_list"
};
this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
let data = res.data;
if (data.code == 1) {
this.moneyList = data.data;
if (this.isNewPerson != true) {
this.selectMoney = this.moneyList[0].money;
this.cashVal = 0;
}
} else {
this.$vux.toast.text(data.msg);
}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
},
getNewPersonData() {

let params = {
token: this.token,
api_name: "newer_case_out_list"
};
this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
let data = res.data;
if (data.code == 1) {
this.newPersonList = data.data;
this.selectMoney = this.newPersonList[0].money;
this.newPersonVal = 0;
} else {
this.$vux.toast.text(data.msg);
}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
},
onItemClick(index, money) {
this.selectMoney = money;
if (index == 1) {
this.cashVal = -1;
} else if (index == 2) {
this.newPersonVal = -1;
}
},
hidePopup() {
this.isShowPopup = false;
},
ShowPopup(title, type, info = "", btnText = "好的") {
this.isShowPopup = true;
this.isCloseBtn = false;
this.isToWallet = false;
this.isToPhone = false;
this.isToGgk = false;
this.popupTitle = title;
this.popupInfo = info;
switch (type) {
case "关闭":
this.isCloseBtn = true;
break;
case "完善":
this.isToWallet = true;
btnText = "去完善";
break;
case "绑定":
this.isToPhone = true;
btnText = "去绑定";
break;
case "刮刮卡":
this.isToGgk = true;
btnText = "去刮卡";
break;
default:
break;
}
this.popupBtnText = btnText;
},
cash() {
if (this.lock) return false;
this.lock = true;
let apiName = "immediately_money";
let plat = 1;
var u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; 
if (isAndroid) {
plat = 2;
}

if (this.newPersonVal != -1 && this.cashVal == -1) {
apiName = "userWithdrawals";
}

let params = {
token: this.token,
api_name: apiName,
select_money: this.selectMoney,
user_money: this.money,
package_name: localStorage.getItem("package_name"),
plat: plat
};
this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
let data = res.data;
this.lock = false;
if (data.code == 1) {

this.ShowPopup(data.data.title1, "关闭", data.data.title2);
this.getInitData();
} else {
console.log(data.code);
switch (data.code) {
case "-1":
this.ShowPopup(data.msg, "关闭");
break;
case "-2":
this.ShowPopup(data.msg, "完善");
break;
case "-3":
this.ShowPopup(data.msg, "绑定");
break;
case "-4":
console.log("去刮卡");
this.ShowPopup(data.msg, "刮刮卡");
break;

default:
break;
}

}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
},
toWallet() {


this.$route.push("/wallet");
},
toPhone() {


this.$router.push("/bind-phone");
},
toGgk() {



window.location.href = "../ggk/ggk-list.html";
}
}
};



